* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  position: relative;
  top: 7.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  box-shadow: rgba(141, 180, 236, 0.15) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.mainReportContainer-1,
.mainReportContainer-2 {
  width: 300px;
  height: 400px;
  padding: 1.5rem;
  border-radius: 15px;
}

.mainReportContainer-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background: linear-gradient(
    to bottom,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
}

.totalScoreCard {
  width: 8rem;
  height: 8rem;
  padding: 1rem;
  border-radius: 50%;
  display: grid;
  place-items: center;
  background: linear-gradient(
    to bottom,
    hsla(256, 72%, 46%, 1),
    hsla(241, 72%, 46%, 0)
  );
}

.totalScoreValue {
  font-size: 3rem;
  color: aliceblue;
}

.scoreGrade {
  font-size: 24px;
  font-weight: 700;
  color: aliceblue;
}

.basicTextColor {
  color: #c8c7ff;
}

.resultPara {
  text-align: center;
  font-size: 18px;
}

.mainReportContainer-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

.summaryHeadText {
  margin-bottom: 1rem;
  color: #303b5a;
}

.Reaction,
.Memory,
.Verbal,
.Visual {
  width: 100%;
  border-radius: 5px;
  padding: 0.8rem 1rem;
  background-color: #f2fbfa;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Reaction {
  background-color: #fff6f5;
}
.Memory {
  background-color: #fffbf2;
}

.Verbal {
  background-color: #f2fbfa;
}
.Visual {
  background-color: #f3f3fd;
}

.Reactiontext {
  color: #ff5555;
}
.Memorytext {
  color: #ffb21e;
}
.Verbaltext {
  color: #00bb8f;
}
.Visualtext {
  color: #1125d6;
}

.exam-subject {
  display: flex;
  align-items: center;
  justify-content: center;
}

.subjectIcon {
  margin-right: 0.5rem;
}

.scoreBox {
  color: "#303b5a";
  font-weight: 500;
}

.scoreBoxTotal {
  color: #99a2a9;
}

.continueBtn {
  width: 100%;
  border: none;
  border-radius: 25px;
  padding: 1rem 1rem;
  background-color: hsl(224, 30%, 27%);
  text-align: center;
  color: aliceblue;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  margin-top: 1rem;
}

.continueBtn:hover {
  background: linear-gradient(
    to bottom,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
}

@media only screen and (max-width: 425px) {
  .App {
    display: block;
    top: 2rem;
  }
}
